.home-page-container{
    padding: 20px 8%;
    background-color: #262626;
    height:100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 8px;
    margin-top: 0.6px;
}
.certificate-main-heading{
    color:#ffffff;
    font-family: "roboto";
}
.chrono-container{
   width:100%;
    height: 500px;
  overflow-y: auto;
  margin-top: 20px;
}

.heading-highlight{
    color:#ef1111
}
.chrono-container-card{
    height:100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content:flex-start;
    background-color: #dbdbdb;
}
.certificate-name-container, .certificate-Img-container{
    width:50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.chrono-img{
    width:100%;
    height:100%;
}
.certificateName{
    font-size: 18px;
    font-family: "roboto";
    color:#1B6778;
    font-weight: 800;
    text-align: center;
}

.chrono-control-panel {
    width: 50px;

  }